* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: block;
  font-family: 'Times Roman';
  width: 100vw;
  height: 100vh;
}
.center {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.headline {
  padding-left: 20px;
  padding-bottom: 7vw;
  padding-top: 3vw;
  border-bottom: 1px solid black;
  margin-bottom: 6vw;
}
.journal {
  width: 60vw;
  padding-left: 1vw;
}
.grid {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(200px, 400px));
  grid-gap: 2vw;
  padding: 20px;
}
.cell {
  width: 400px;
  height: 450px;
  margin: 0;
  padding: 0;
  border: none;
  overflow: hidden;
  scrollbar-width: none;

  @media (max-width: 420px) {
    width: 95vw;
    height: 400px;
  }

  &::-webkit-scrollbar {
    display: none;
  }
}
